body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

html{height:100%}body{height:100%;overflow:hidden}*{box-sizing:border-box}.appContainer{height:100%}.App{text-align:center;height:100%;display:flex}.App .app-toolbars{height:100px}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.design-selector{display:inline-block;border:2px solid black;padding-left:2px;padding-right:2px;margin-right:3px;font-size:large}.design-selector:hover{background-color:lightgreen}.design-selector-active{font-weight:bold;background-color:lightblue}.menu{display:flex}.menu .menu-item{width:32px;height:32px}.menu .selected{background-color:lightgreen}.menu-side{flex-direction:column}.main-layout{border-collapse:collapse}.main-layout tr td{border:1px solid gray;overflow:hidden;vertical-align:top}.main-layout-div .main-layout-row{display:flex}.main-layout-div .main-layout-row .main-layout-cell{border:1px solid black;overflow:hidden}.side-drawing-container{display:flex;flex-direction:column;margin-left:auto;margin-right:auto;border:3px solid black;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}

.icon-container .icon{touch-action:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.icon-container .icon-separator{height:32px;background:gray;margin-top:2px;margin-bottom:2px;width:4px !important;margin-right:2px;margin-left:2px}.icon-container .icon-bordered{margin:2px;border:2px solid black;border-radius:3px;padding:2px;background:#f0f0f0}.icon-container .icon-bordered:hover{background:#d0d0d0}.icon-container .icon-hidden{display:none}.icon-container .icon-readonly{opacity:0.5}.icon-container .icon-readonly:hover{cursor:not-allowed}.icon-container .icon-small{width:24px;height:24px}.icon-container .icon-medium{width:32px;height:32px}.icon-container .icon-large{width:48px;height:48px}.icon-container .icon-selected{background-color:lightgreen}.icon-container .icon-selected:hover{background:green}

.drawing-canvas{border:1px solid lightgray;padding:1px;background-color:antiquewhite;display:block;touch-action:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.drawing-canvas-section-selector{background-color:lightblue;touch-action:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.section-selector{text-align:left;text-align:initial}

.modal .modal-backdrop{position:fixed;left:0;right:0;top:0;bottom:0;z-index:10000;background-color:lightgray;opacity:0.7}.modal .modal-container{position:fixed;left:0;right:0;top:0;bottom:0;z-index:10000}.modal .modal-container .modal-dialog{position:relative;padding:5px;background-color:white;border:2px solid black;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;min-width:640px;font-size:large;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%) !important}.modal .modal-container .modal-dialog .modal-header{display:flex;font-size:large}.modal .modal-container .modal-dialog .modal-header .modal-header-icon{display:inline-block;padding-right:5px}.modal .modal-container .modal-dialog .modal-header .modal-header-title{display:inline-block;padding-right:5px;font-weight:bold}.modal .modal-container .modal-dialog .modal-body{border-top:1px solid gray;margin-top:5px;overflow-x:hidden;overflow-y:auto;max-height:90vh}.modal .modal-container .modal-dialog .modal-footer{text-align:right;padding-top:5px;padding-bottom:5px;border-top:1px solid gray;margin-top:5px}.modal .modal-button{display:inline-block;min-width:60px;background-color:blue;color:white;margin-left:10px;text-align:center;font-size:larger;font-weight:bold;text-transform:uppercase;cursor:pointer;padding:0px 5px 0px 5px}.modal .modal-button:hover{background-color:green}

.form .form-label{font-weight:bold}.form .form-control-container{text-align:left}.form .form-control-container .form-control{width:100%;font-size:large}.form .form-control-container input[type="checkbox"]{width:auto;width:initial}

.toolbar-legend{width:100%}.toolbar-legend .toolbar-legend-icon{width:40px}.toolbar-legend .toolbar-legend-separator{background-color:gray;height:3px;margin-bottom:2px}

.tutorial .tutorial-section header{text-align:center;text-transform:uppercase;font-weight:bold;background-color:lightblue}

.modal-side-editor{width:90vw;height:90vh}

